@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap");

:root {
  --header-height: 3rem;
  --hue-color: 250;
  --first-color: hsl(var(--hue-color), 69%, 61%);
  --first-color-second: hsl(var(--hue-color), 69%, 61%);
  --first-color-alt: hsl(var(--hue-color), 57%, 53%);
  --first-color-lighter: hsl(var(--hue-color), 92%, 85%);
  --title-color: hsl(var(--hue-color), 8%, 15%);
  --text-color: hsl(var(--hue-color), 8%, 45%);
  --text-color-light: hsl(var(--hue-color), 8%, 65%);
  --input-color: hsl(var(--hue-color), 70%, 96%);
  --body-color: hsl(var(--hue-color), 60%, 99%);
  --scroll-bar-color: hsl(var(--hue-color), 12%, 90%);
  --scroll-thumb-color: hsl(var(--hue-color), 12%, 80%);
  --container-color: #fff;
  --body-font: "Poppins", sans-serif;
  --box-shadow: 0px 0.5rem 1.5rem rgba(0, 0, 0, 0.5);
  --big-font-size: 2rem;
  --h1-font-size: 1.5rem;
  --h2-font-size: 1.25rem;
  --h3-font-size: 1.125rem;
  --normal-font-size: 0.938rem;
  --small-font-size: 0.813rem;
  --smaller-font-size: 0.75rem;
  --font-medium: 500;
  --font-semi-bold: 600;
  --mb-0-25: 0.25rem;
  --mb-0-5: 0.5rem;
  --mb-0-75: 0.75rem;
  --mb-1: 1rem;
  --mb-1-5: 1.5rem;
  --mb-2: 2rem;
  --mb-2-5: 2.5rem;
  --mb-3: 3rem;
  --z-tooltip: 10;
  --z-fixed: 100;
  --z-modal: 100;

  @media screen and (min-width: 968px) {
    :root {
      --big-font-size: 3rem;
      --h1-font-size: 2.25rem;
      --h2-font-size: 1.5rem;
      --h3-font-size: 1.25rem;
      --normal-font-size: 1rem;
      --small-font-size: 0.875rem;
      --smaller-font-size: 0.813rem;
    }
  }
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 0 0 var(--header-height) 0;
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
  background-color: var(--body-color);
  color: var(--text-color);
}

h1,
h2,
h3,
h4 {
  color: var(--title-color);
  font-weight: var(--font-semi-bold);
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
}

.padding__bottom{
  padding-bottom: 1rem;
}

.container {
  max-width: 768px;
  margin-left: var(--mb-1-5);
  margin-right: var(--mb-1-5);
}

.grid {
  display: grid;
  gap: 1.5rem;
}

.header {
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: var(--z-fixed);
  background-color: var(--body-color);
  box-shadow: 0 -1px 4px rgba(0, 0, 0, 0.15);
}

.nav {
  max-width: 968px;
  height: var(--header-height);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav__logo,
.nav__toggle {
  color: var(--title-color);
  font-weight: var(--font-medium);
}

.nav__logo:hover {
  color: var(--first-color);
}

.nav__toggle {
  font-size: 1.1rem;
  cursor: pointer;
}


.section {
  margin: 2rem 1rem 4rem;
}

.section__title {
  font-size: var(--h1-font-size);
}

.section__subtitle {
  display: block;
  font-size: var(--small-font-size);
}

.section__title,
.section__subtitle {
  text-align: center;
}
.nav_toggle:hover {
  color: var(--first-color);
}

.flexbox-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 1440px;
  margin: 0 auto;
  padding: 1rem;
  gap: 2rem;
}

.countUpTimer {
  text-align: center;
  margin: 1rem;

}

.flexBoxContainer {
display: flex;
justify-content: flex-start;

*:first-child {
  align-self: flex-start;
}

*:nth-child(2) {
  margin-left: 1rem;
}
}

.flexbox__item {
  position: relative;
  max-width: 350px;
  border-radius: 2rem;
  box-shadow: var(--box-shadow);
  /* box-shadow: 0px 0.5rem 1.5rem rgba(0, 0, 0, 0.5); */
}

.item-text-box > h3,
.item-text-box > h4 {
  padding: 1rem 0;
}

.item__image {
  max-width: 350px;
  max-height: 200px;
  border-radius: 2rem 2rem 0 0;
  aspect-ratio: 128/73;
}

.item__heading {
  text-align: left;
  padding: 1rem 0;
}

.item__heading:hover,
.item__text:hover {
  color: var(--first-color);
  transition: 0.5s;
}

.item-text-box {
  padding: 1rem 1rem 2rem 1rem;
}

.scrollUpButton {
position: fixed;
right: 1rem;
bottom: -30%;
background-color: white;
box-shadow: 0 4px 12px hsla(0, 0%, 20%, 0.1);
border-radius: 0.5rem;
display: inline-flex;
padding: 0.25rem;
z-index: var(--z-tooltip);
opacity: 0.8;
transition: 0.4s;
}

.scrollUpButton:hover {
    opacity: 1;
  }

.scrollup__icon {
    font-size: 1.5rem;
    color: var(--first-color);
}

.show-scroll {
    bottom: 3.5rem;
}


@media screen and (max-width: 767px) {
  .nav__menu {
    position: fixed;
    bottom: -100%;
    left: 0;
    width: 100%;
    background-color: var(--body-color);
    padding: 2rem 1.5rem 4rem;
    box-shadow: 0 -1px 4px rgba(0, 0, 0.15);
    border-radius: 1.5rem 1.5rem 0 0;
    transition: 0.3s;
  }
}

.nav_list {
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
}

.currentEmployer {
  border-radius: 5px;
  padding: 0.1rem;
  color: green;
  animation-name: fadeAndShine;
  animation-duration: 2s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
}

@keyframes fadeAndShine {  
  100% {
    opacity: 0.5;
  } 
  50% {
    opacity: 1;
  }
  0% {
    opacity: 0.5;
  }
}



.nav__link {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: var(--small-font-size);
  color: var(--title-color);
  font-weight: var(--font-medium);
}

.nav__link:hover {
  color: var(--first-color);
}

.nav__icon {
  display: flex;
  justify-content: center;
  padding-bottom: 1rem;
}

.nav__icon svg {
  height: 20px;
  width: 20px;
}

.nav__close {
  position: absolute;
  right: 1.3rem;
  bottom: 0.5rem;
  font-size: 1.5rem;
  cursor: pointer;
  color: var(--first-color);
}

.nav__close:hover {
  color: var(--first-color-alt);
}

.show-menu {
  bottom: 0;
}

.home__container {
  gap: 1rem;
}

.home__content {
  grid-template-columns: 0.5fr 3fr;
  padding-top: 3.5rem;
  align-items: center;
}

.home__social {
  display: grid;
  grid-template-columns: max-content;
  row-gap: 1rem;
  color: var(--title-color);
}

/* Needed */
.social-icon-home {
  font-size: 1.25rem;
  color: var(--first-color);
  padding: 0.5rem;
  display: flex;
}

/* Needed */
.social-icon-footer {
  font-size: 1.25rem;
  color: #fff; 
  padding: 0.5rem;
  display: flex;
}

/* Needed */
.social-icon-home:hover,
.social-icon-footer:hover
 {
  color: #fff;  
  background-color: var(--first-color-lighter);
  border-radius: 0.8rem;
  transition: 0.3s;
}

.home__blob {
  width: 200px;
  fill: var(--first-color);
}

.home__blob-img {
  width: 170px;
  aspect-ratio: 1/1;
}

.home__data {
  grid-column: 1/3;
}

.home__title {
  font-size: var(--big-font-size);
}

.home__subtitle {
  font-size: var(--h3-font-size);
  color: var(--text-color);
  font-weight: var(--font-medium);
  margin-bottom: var(--mb-0-75);
}

.home__description {
  margin: var(--mb-1) 0;
}

.home__scroll {
  display: none;
}

.home__scroll-button {
  color: var(--first-color);
  transition: 0.3s;
}

.home__scroll-button:hover {
  transform: translateY(0.25rem);
}

.home__scroll-mouse {
  font-size: 2rem;
}

.home__scroll-name {
  font-size: var(--small-font-size);
  color: var(--title-color);
  font-weight: var(--font-medium);
  margin-right: var(--mb-0-25);
}

.home__scroll-arrow {
  font-size: 1.25rem;
}

.button {
  display: inline-block;
  background-color: var(--first-color);
  color: #fff;
  padding: 1rem;
  border-radius: 0.5rem;
  font-weight: var(--font-medium);
}

.button:hover {
  background-color: var(--first-color-alt);
  box-shadow: 0px 0.5rem 1.5rem rgba(0, 0, 0, 0.5);
}

.button__icon {
  font-size: 1.25rem;
  margin-left: var(--mb-0-5);
  transition: 0.3s;
}

.button--flex {
  display: inline-flex;
}

.social__mediaLinks {
  font-size: 1.25rem;
  color: var(--first-color);
  padding: 0.3rem;

} 

.social__mediaLinks:hover {
  color: #fff;
  background-color: var(--first-color-lighter);
  border-radius: 1rem;
  transition: 0.3s;
}


.footer {
  padding-top: 2rem;
}

.footer__container {
  row-gap: 3.5rem;
}

.footer__bg {
  background-color: var(--first-color-second);
  padding: 2rem 0 3rem;
}

.footer__title {
  font-size: var(--h1-font-size);
  margin-bottom: var(--mb-0-25);
}

.footer__subtitle {
  font-size: var(--small-font-size);
}

.footer__links {
  display: flex;
  flex-direction: column;
  row-gap: 1.5rem;
}

.footer__links:hover {
  color: var(--first-color-lighter);
}

.footer__socials {
  font-size: 1.25rem;
  margin-right: var(--mb-1-5);
  display: flex;
  gap: 1rem;
}

.footer__socials:hover {
  color: var(--first-color-lighter);
}

.footer__copy {
  font-size: var(--smaller-font-size);
  text-align: center;
  color: #fff;
  margin-top: var(--mb-3);
}

.footer__title,
.footer__subtitle,
.footer__links,
.footer__socials {
  color: #fff;
  display: flex;
}

body.dark-theme {
  --first-color-second: hsl(var(--hue-color), 30%, 8%);
  --title-color: hsl(var(--hue-color), 8%, 95%);
  --text-color: hsl(var(--hue-color), 8%, 75%);
  --input-color: hsl(var(--hue-color), 29%, 16%);
  --body-color: hsl(var(--hue-color), 28%, 12%);
  --container-color: hsl(var(--hue-color), 29%, 16%);
  --scroll-bar-color: hsl(var(--hue-color), 12%, 48%);
  --scroll-thumb-color: hsl(var(--hue-color), 12%, 36%);
  --box-shadow: 0.1rem 0.1rem 0.4rem rgb(255, 255, 255);
}

.nav__btns {
  display: flex;
  align-items: center;
}

.change-theme {
  font-size: 1.25rem;
  color: var(--title-color);
  margin-right: var(--mb-1);
  cursor: pointer;
  border: none;
  background-color: inherit;
}

.change-theme:hover {
  color: var(--first-color);
}


::-webkit-scrollbar {
  width: 0.6rem;
  background-color: var(--scroll-bar-color);
  border-radius: 0.5rem;
}

::-webkit-scrollbar-thumb {
  background-color: var(--scroll-thumb-color);
  border-radius: 0.5rem;
}

::-webkit-scrollbar-thumb:hover {
  background-color: var(--text-color-light);
}


@media screen and (max-width: 350px) {
  .container {
    margin-left: var(--mb-1);
    margin-right: var(--mb-1);
  }

  .nav__menu {
    padding: 2rem 0.25rem 4rem;
  }

  .nav__list {
    column-gap: 0;
  }

  .home__content {
    grid-template-columns: 0.25fr 3fr;
  }

  .home__blob {
    width: 180px;
  }
}

@media screen and (min-width: 568px) {
  .home__content {
    grid-template-columns: max-content 1fr 1fr;
  }

  .home__data {
    grid-column: initial;
  }

  .home__img {
    order: 1;
    justify-self: center;
  }

  .footer__container {
    grid-template-columns: repeat(2, 1fr);
  }

  .footer__links {
    flex-direction: row;
    column-gap: 2rem;
  }
}

@media screen and (min-width: 768px) {
  .container {
    margin-left: auto;
    margin-right: auto;
    padding-left: 2rem;
    padding-right: 2rem;
  }

  body {
    margin: 0;
  }

  .header {
    top: 0;
    bottom: initial;
  }

  .header,
  .main,
  .footer__container {
    padding: 0 1rem;
  }

  .nav {
    height: calc(var(--header-height)+5rem);
    column-gap: 1rem;
    padding: 2rem 0
  }

  .section {
    padding: 3rem 0 2rem;
  }

  .nav__icon,
  .nav__close,
  .nav__toggle {
    display: none;
  }

  .nav__list {
    display: flex;
    column-gap: 2rem;
  }

  .nav__menu {
    margin-left: auto;
  }

  .change-theme {
    margin: 0;
  }

  .home__container {
    row-gap: 5rem;
  }

  .home__content {
    padding-top: 5.5rem;
    column-gap: 2rem;
  }

  .home__blob {
    width: 270px;
  }

  .home__scroll {
    display: block;
  }

  .home__scroll-button {
    margin-left: 3rem;
  }

  .footer__container {
    grid-template-columns: (3, 1fr);
  }

  .footer__bg {
    padding: 1rem 0 1.5rem;
  }

  .footer__copy {
    margin-top: 1.5rem;
  }
}

@media screen and (min-width: 1024px) {
  header,
  footer,
  .footer__container {
    padding: 0;
  }

  .home__blob {
    width: 280px;
  }

  .home__social {
    transform: translateX(-6rem);
  }
}
